.cards {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    padding-left: 2rem;
    padding-right: 2rem;
    @media ($tablet) {
        justify-content: space-between;
    }
    @media ($laptop) {
        justify-content: space-around;
    }
    @media ($projector) {
        justify-content: space-between;
        padding: 0;
    }
}

