.container-lable {
    width: 100%;
    max-width: 125rem;
    padding: 0 2rem;
    margin: 0 auto;
    @media ($desktop) {
        padding: 0 2rem;
    }
}

.container-section {
    width: 100%;
    max-width: 114rem;
    margin: 0 auto;
}

// Кнопка вверх
.pageup {
    opacity: 0;
    width: 26px ;
    height: 26px;
    position: fixed;
    bottom: 100px;
    right: 5%;
    z-index: 1000;
    background-color: #e05926;
    cursor: pointer;
    border: 1px solid #222222;
    transition: 1s all;
    overflow: hidden;
}