 // Основные стили
 .competenceCard {
     &__wrap  {
         @include cards();
         margin-top: 3rem;
     }
     &__js {
         @include cards();
         height: auto !important;
         @media ($tablet) {
             height: 500px !important;
         }
         @media ($laptop) {
             height: 500px !important;
         }
         @media ($desktop) {
             width: 100% !important;
             height: 390px !important;
             padding-left: 336px;
         }
     }
     &__heightJsCard {
         min-height: 29.5rem;
         @media ($phone) {
             height: 26rem;
         }
         @media ($tablet) {
             height: 39rem;
         }
         @media ($desktop) {
             height: 25.5rem;
         }
     }
     &__img {
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: auto;
        opacity: 0.5;
        transition: opacity 1s;
    }
     &__desc {
        position: relative;
        height: 100%;
        background: radial-gradient(circle at left,rgba(0,0,0,.6) 0,#000 25%);
        padding: 2rem;
        z-index: 50;
    }
    &__main {
        min-height: 29.5rem;
        @media ($phone) {
            height: 26rem;
        }
        @media ($tablet) {
            height: 39rem;
        }
        @media ($laptop) {
            height: 34rem;
        }
        @media ($desktop) {
            height: 32rem;
        }
        @media ($projector) {
            height: 29.5rem;
        }
    }
    &__title {
        @include titleCard();
        font-size: 2.1rem;
        line-height: 31.5px;
        @media ($phone) {
            font-size: 2.7rem;
            line-height: 40.5px;
        }
    }
    &__subtitle {
        @include subTitleCard();
        font-size: 1.8rem;
        line-height: 27px;
        @media ($phone) {

        }
    }
    &__lable {
        display: flex;
        align-items: center;
        margin: 1rem 0 1.5rem;
    }
    &__icon {
        flex-shrink: 0;
        width: 6.8rem;
        height: 6.8rem;
        border-radius: 50%;
        margin-right: 1.5rem;
        @media ($phone) {
            width: 7.8rem;
            height: 7.8rem;
        }
    }
    &__iconTxt {
        font-size: 3.3rem;
        font-weight: 600;
        padding-left: 20px;
        padding-top: 30px;
        @media ($phone) {
            font-size: 4rem;
        }
    }
    &__position {
        @include positionTitleCard();
    }
    &__txt {
        @include descCard();
    }
    &__statistic {
        @include statisticCard();
        margin-top: 1rem;
    }
    &__link {
        @include btn();
        display: block;
        font-size: 14px;
        color: $white;
        background-color: $btnCard;
        border-radius: 40px;
        text-align: center;
        vertical-align: middle;
        line-height: 1.25;
        padding: 10px 20px;
        white-space: nowrap;
        &:hover {
            background-color: $btnCardHover;
        }
        @media ($desktop) {
            display: inline-block;
        }
    }
    &__link-lightColor {
        background-color: $btnCard2;
        margin-top: 1rem;
        &:hover {
            background-color: $btnCardHover2;
        }
        @media ($desktop) {
            margin-top: 0;
            margin-left: 1rem;
        }
    }
    &__footer {
        @media ($tablet) {
            margin-top: 2rem !important;
        }
        @media ($desktop) {
            margin-top: 0 !important;
        }
    }
 }