.filter {
  position: relative;
  background-color: $backgroundFilter;
  margin-top: 7rem;
  &__items {
    display: flex;
    justify-content: flex-start;
    align-content: center;
    overflow: hidden;
  }
  &__item {
    cursor: pointer;
    &:hover {
      .filter__link  {
        color: $colorLinksActive;
      }
    }
  }
  &__btn {
    @include btn();
    display: inline-block;
    color: $colorLinks;
    font-size: 1.6rem;
    text-transform: capitalize;
    padding: 1rem;
  }
  &__link-active {
    color: $colorLinksActive;
    background-color: #0a57a3;
    font-weight: 500;
  }
}