.animated {
   animation-duration: 1s;
   animation-fill-mode: both;
}

.fadeIn {
   animation-name: fadeIn;
}

.fadeOut {
   animation-name: fadeOut;
}

// Появление основновного меню
.showMenu {
   animation: showMenu 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

.hideMenu {
   animation: hideMenu 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
   
.opacityShow {
   animation-name: opacityShow;
   animation-duration: 0.3s;
}
   
.rotate-90-ccw {
   animation: rotate-90-ccw 0.4s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}
   
.rotate-anti-90 {
   animation: rotate-anti-90 0.6s cubic-bezier(0.250, 0.460, 0.450, 0.940) both;
}

// Появление карточек
.scale-up-hor-right {
   animation: scale-up-hor-right 0.8s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}

// Появление карточек 2
.scale-up-top {
   animation: scale-up-top 0.8s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
}


// Keyframes

// Animation
@keyframes fadeIn {
   0% {
      opacity: 0;
   }
   100% {
      opacity: 1;
   }
}

@keyframes fadeOut {
   0% {
      opacity: 1;
   }
   100% {
      opacity: 0;
   }
}

// Всплытие Меню
@keyframes showMenu {
   0% {
      transform: scaleY(0.4);
      transform-origin: 100% 0%;
   }
   100% {
      transform: scaleY(1);
      transform-origin: 100% 0%;
   }
}

@keyframes hideMenu {
   0% {
      transform: scaleY(1);
      transform-origin: 100% 0%;
   }
   100% {
      transform: scaleY(0);
      transform-origin: 100% 0%;
   }
}

@keyframes opacityShow {
   0% {
      opacity: 0;
   }
   70% {
      opacity: 0;
   }
   100% {
      opacity: 1;
   }
}


// Кнопка-крестик
@keyframes rotate-90-ccw {
   0% {
      transform: rotate(0);
   }
   100% {
      transform: rotate(-45deg) translate(2.5px, -2.5px);
   }
}

@keyframes rotate-anti-90 {
   0% {
      transform: rotate(-45deg) translate(2.5px, -2.5px);
   }
   100% {
      transform: rotate(0)
   }
}

// Появление карточек
@keyframes scale-up-hor-right {
   0% {
      transform: scaleX(0.4);
      transform-origin: 100% 100%;
   }
   100% {
      transform: scaleX(1);
      transform-origin: 100% 100%;
   }
}

// Появление карточек2
@keyframes scale-up-top {
   0% {
      transform: scale(0.5);
      transform-origin: 50% 0%;
   }
   100% {
      transform: scale(1);
      transform-origin: 50% 0%;
   }
}




