.carousel {
    position: relative;
    height: 17rem;
    width: auto;
    background-color: #161616;
    margin-top: 3rem;
    padding: 1rem 0 0 2rem;
    @media ($projector) {
        padding: 1rem 0 0;
    }
    &__section {
        overflow: hidden;
        @media ($projector) {
            overflow: auto;
        }
    }
    &__window {
        width: 1120px;
        overflow: hidden;
        margin: 0 auto;
    }
    &__list {
        display: flex;
        transition: 0.5s all;
    }
    &__item {
        flex-shrink: 0;
        width: 20rem;
        height: 10rem;
        border-radius: 5px;
        margin-right: 3rem;
        color: white;
        font-size: 2.8rem;
        font-weight: bold;
        text-align: center;
        padding-top: 6rem;
    }
    &__nav {
        position: absolute;
        width: 10rem;
        left: 195px;
        bottom: -5.3rem;
        display: flex;
        justify-content: space-between;
        @media ($phone) {
          left: 450px;
          bottom: -5.3rem;
        }
        @media ($tablet) {
          left: 650px;
          bottom: -5.3rem;
        }
        @media ($laptop) {
          left: 850px;
          bottom: -5.3rem;
        }
        @media ($projector) {
          left: 1290px;
          bottom: -5.3rem;
        }
    }
    &__btn {
        padding: 1rem;
    }
}

.carousel > ol {
    top: 140px !important;
    @media ($laptop) {
        top: 0 !important;
    }
}

.carousel > ol > li{
    display: none !important;
    @media ($phone) {
        display: list-item !important;
    }
}

